<template>
  <div id="useHelp">
    <div class="Content">
       <div class="Radio">
         <van-radio-group v-model="radio">
           <van-radio name="1" checked-color="#FF8D59">建议</van-radio>
           <van-radio name="2" checked-color="#FF8D59">问题</van-radio>
         </van-radio-group>
       </div>
      <div class="textVue">
        <textarea  v-model="textarea"   placeholder="请详细描述你的问题"></textarea>
        <div class="outImg">
            <van-uploader v-model="fileList" multiple  :max-count="9"  :after-read="afterRead"  @delete="Delete" />
            <span class="addName">添加图片</span>
        </div>
      </div>
      <button @click="opinionFeedBack">提交反馈</button>
    </div>
  </div>
</template>
<script>
  import { Dialog } from 'vant';

  export default {
    name: "",
    components: {
      [Dialog.Component.name]: Dialog.Component
    },
    data () {
      return {
        radio: '1',
        textarea:"",
        imgArr:[],
        fileList: [
        ],
        parms:{
          file:""
        }
      }
    },
    beforeCreate () {
      document.querySelector('html').setAttribute('style', 'height:100%;font-size:37.5px;');
      document.querySelector('body').setAttribute('style', 'height:100%;background: #f7f7f7;');
    },
    mounted(){
      this.token=this.$store.token;
    },
    methods:{
      Delete(file){
        this.afterRead();
      },
     afterRead() {
        // 此时可以自行将文件上传至服务器
       var that=this;
       let formData=new FormData();
       for(let i=0;i<this.fileList.length;i++){
         formData.append('files',this.fileList[i].file);
       }
       this.http.post("upload/img/multi",formData)
          .then(res => {
             if(res.data==null){
               that.imgArr=[];
             }else {
               that.imgArr=res.data.urls;
             }
          })
        },
        opinionFeedBack() {
        // 此时可以自行将文件上传至服务器
           let data={
                 imgUrls:this.imgArr,
                 freeBackType:parseInt(this.radio),
                 problemRemark:this.textarea,
                 submitter:""
               }
                 console.log(data)
          this.http.post2("opinionFeedBack",data,this.token)
          .then(res => {
               console.log(res);
            if(res.data.resultCode==1){
              Dialog.alert({
                message: '反馈成功!'
              }).then(() => {
                this.$router.back(-1);
              });
            }else {
              Dialog.alert({
                message: res.data.resultMsg
              }).then(() => {
              });
            }
          })
      }
    }
  }
</script>
<style scoped  lang="scss"  >

  #useHelp{
    background:#F8F8F8 ;
    min-height: 100%;
    .outImg{
      overflow: hidden;
    }
    van-radio{
      display: inline-block;
    }
    .Content{
      padding: 40px;
      box-sizing: border-box;
    }
    .textVue{
      width:100%;
      min-height:480px;
      background:rgba(255,255,255,1);
      border-radius:8px;
      padding:32px;
      box-sizing: border-box;
      .addName{
        float: left;
        color:#999999;
        font-size:24px;
        font-family:PingFangSC-Regular;
        font-weight:400;
        line-height: 124px;
      }
      textarea{
        height: 284px;
        width: 100%;
        color:#484848;
        border: none;
      }
      .van-uploader{
        float: left;
      }
    }
    .Radio{
      height:72px;
      width: 100%;
    }
    button{
      width:100%;
      height:88px;
      background: #FF8D59;
      border-radius:8px;
      border: none;
      margin-top: 56px;
      font-size:26px;
      font-family:PingFangSC-Medium;
      font-weight:500;
      color:rgba(255,255,255,1);
    }
  }
  .van-radio{
    float: left;
    margin-right: 50px;
  }
  .header {
    text-align: center;
    color: #fff;
    position: relative;
    height: 88px;
    background:rgba(255,255,255,1);
  }
  .header  span{
    font-size:32px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    line-height: 88px;
  }
  .header i{
    content: "";
    position: absolute;
    top: 13px;
    left: 30px;
    margin: 20px;
    border-right: 4px solid rgba(51,51,51,1);
    border-bottom: 4px solid rgba(51,51,51,1);
    width: 18px;
    height: 18px;
    transform: matrix(-0.71,-0.71, -0.71, 0.71, 0, 0)
  }


</style>



